@import url('https://fonts.googleapis.com/css?family=Montserrat');
@import url('https://fonts.googleapis.com/css?family=Playfair+Display');
@import url('https://fonts.googleapis.com/css?family=Playfair+Display:500');
@import url("https://fonts.googleapis.com/css2?family=Libre+Barcode+39+Text&display=swap");
@import url('https://fonts.googleapis.com/css?family=Quicksand');
@import url('https://fonts.googleapis.com/css?family=IBM+Plex+Mono');

html, body, #root {
    height: 100vh;
    letter-spacing: 1px;
    font-family: Lato,'Helvetica Neue',Arial,Helvetica,sans-serif;
    margin: 0;
    overflow: hidden;
}

h1, h2, h3, h4 {
    font-family: 'Montserrat';
}

.fullHeight {
    height: 100%;
}

p.smallMargin {
    margin: 0.5em 0;
}

@keyframes banner {
    from {left:-195%;}
    to {left:0px;}
}

@keyframes line {
    from {left:-210%;}
    to {right:0px;}
}

@keyframes plane {
    from {left:-80%;}
    to {left:150%;}
}

@keyframes notification {
    0% {background-color: transparent; color: transparent;}
    20% {background-color: #1e6067; color: white;}
    80% {background-color: #1e6067; color: white;}
    100% {background-color: transparent; color: transparent;}
}